
.main-view {
  display: block;
  max-height: 100vh;
}
.title-container {
  position: absolute;
  bottom: 0;
  top: 0;
  margin: auto;
  padding: 40px;
  left: 0;
  right: 0;
  width: 60%;
  height: 60%;
  min-width: 500px;
  max-width: 800px;
  text-align: center;
  max-height: 800px;
  min-height: 600px;
}
.js-logo {
  height: 150px;
  margin-left: -10px;
  box-shadow: 7px 4px #000;
  position: relative;
  top: 40px;
}
h1 {
  font-family: Roboto;
  position: relative;
}
body {
  /* background-image: url("https://image.ibb.co/j75Tpn/bg.jpg"); */
  background-repeat: no-repeat;
  background-size: cover;
  background: linear-gradient(to top, #136a8a, #267871);

  overflow: hidden;
}
.title {
  font-family: "Quicksand", "Source Sans Pro", -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif; /* 1 */
  display: block;
  font-weight: 700;
  font-size: 100px;
  color: #fff;
  text-shadow: 8px 8px #000;
}
.title-container p {
  color:#d8d8d8;
  text-shadow: 2px 2px #000;
    font-weight: 500;
    margin-top: -20px;
    margin-bottom: 0px;
    font-size: 38px;
}




